import { useState } from "react";
import IUIMenuItem from "./shared/IUIMenuItem"
import { useSelector } from 'react-redux'
import { Prev } from "react-bootstrap/esm/PageItem";

const LeftMenu = (props) => {
    const privileges = useSelector((state) => state.api.loggedInUser?.privileges)

    let schema = [
        {
            name: "home", text: "Home", icon: "home", path: "/home"
        },
        {
            name: "itemManagement", text: "Item Master", icon: "vector-square",
            schema: [
                { name: "itemGroupManagement", text: "Groups", icon: "cubes-stacked", path: "/itemgroups", access: "itemGroup" },
                { name: "itemMasterManagement", text: "Items", icon: "vector-square", path: "/itemmasters", access: "itemMaster" },
            ]
        },
        {
            name: "projectManagement", text: "Project Management", icon: "sitemap",
            schema: [
                { name: "projectManagement", text: "Projects", icon: "sitemap", path: "/projects", access: "project" },
                { name: "towerManagement", text: "Towers", icon: "industry", path: "/towers", access: "tower" },
                { name: "floorManagement", text: "Floors", icon: "cubes", path: "/floors", access: "floor" },
                { name: "flatManagement", text: "Flats", icon: "bed", path: "/flats", access: "flat" },
            ]
        },
        {
            name: "configuration", text: "Administration", icon: "cogs",
            schema: [
                { name: "userManagement", text: "User Management", icon: "users", path: "/users", access: "user" },
                { name: "roleManagement", text: "Role Management", icon: "sitemap", path: "/roles", access: "role" }
            ]
        },
        /*{
            name: "sample", text: "Sample", icon: "cubes-stacked",
            schema: [
                { name: "sampleItem", text: "Sample Item", icon: "etsy", path: "/home" },
                name: "sub", text: "Sub Sample", icon: "universal-access",
                schema: [
                    { name: "sampleItem", text: "Sample Item", icon: "etsy", path: "/" },
                ]
            },
            ]
        }*/
    ];

    // Filter  menu schema based on privileges
    const filterMenu = (s) => {
        if (s.schema) {
            s.schema.forEach(item => {
                item.visible = filterMenu(item);
            })
            return s.schema.some(sch => sch.visible) 
        }

        if (s.access) {
            if (privileges?.some(p => p.module === s.access)) {
                return true;
            }
            else {
                return false;
            }
        } else {
            return true;
        }
    }

    schema.forEach(item => {
        item.visible = filterMenu(item);
    })

    const [showLeftMenu, setShowLeftMenu] = useState(props.leftMenuState);

    return (
        <div className="app-sidebar sidebar-shadow">
            <div className="app-header__logo">
                <div className="logo-src"></div>
                <div className="header__pane ml-auto">
                    <div>
                        <button type="button" className="hamburger close-sidebar-btn hamburger--elastic"
                            onClick={(event) => setShowLeftMenu(!showLeftMenu)}>
                            <span className="hamburger-box">
                                <span className="hamburger-inner"></span></span>
                        </button>
                    </div>
                </div>
            </div>
            <div className="app-header__mobile-menu">
                <div>
                    <button type="button" className="hamburger hamburger--elastic mobile-toggle-nav"
                        onClick={(event) => setShowLeftMenu(!showLeftMenu)}>
                        <span className="hamburger-box">
                            <span className="hamburger-inner"></span></span>
                    </button>
                </div>
            </div>
            <div className="app-header__menu">
                <span>
                    <button type="button"
                        className="btn-icon btn-icon-only btn btn-primary btn-sm mobile-toggle-header-nav"
                        onClick={(event) => setShowLeftMenu(!showLeftMenu)}>
                        <span className="btn-icon-wrapper">
                            <i className="fa fa-ellipsis-v fa-w-6"></i></span>
                    </button>
                </span>
            </div>
            <div className="scrollbar-sidebar">
                <div className="app-sidebar__inner">
                    <ul className="vertical-nav-menu">
                        <li className="app-sidebar__heading">Project Management</li>
                        <IUIMenuItem schema={schema} />
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default LeftMenu