import IUIList from "../../common/IUIList";
import IUIPage from "../../common/IUIPage"

export const ListItemGroup = () => {

    const schema = {
        module: 'itemGroup',
        title: 'Item Group Management',
        paging: true,
        searching: true,
        editing: true,
        adding: true,
        fields: [
            { text: 'Name', field: 'name', type: 'link', sorting: true, searching: true },
            { text: 'Description', field: 'description', type: 'text', sorting: false, searching: false }
        ]
    }


    return (<IUIList schema={schema} />)
}

export const ViewItemGroup = () => {
    const schema = {
        module: 'itemGroup',
        title: 'Item Group Management',
        editing: true,
        adding: false,
        back: true,
        readonly: true,
        fields: [
            {
                type: "area", width: 12
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', type: 'text', required: true, width: 6 },
                    { text: 'Description', field: 'description', type: 'textarea', required: true, width: 12 }
                ]
            },
            {
                type: "area", width: 12
                , fields: [
                    {
                        type: 'module-relation',
                        schema: {
                            module: 'itemMaster',
                            relationKey: "ItemGroupId",
                            title: 'Related Items',
                            paging: true,
                            searching: true,
                            editing: false,
                            adding: false,

                            fields: [
                                { text: 'Name', field: 'name', type: 'text', sorting: true, searching: true },
                                { text: 'Type', field: 'itemType', type: 'text', sorting: true, searching: true },
                                { text: 'Description', field: 'description', type: 'text', sorting: false, searching: false }
                            ]
                        },
                    }
                ]
            }
        ]
    }

    return (<IUIPage schema={schema} />)
}

export const EditItemGroup = () => {
    const schema = {
        module: 'itemGroup',
        title: 'Item Group',
        back: false,
        fields: [
            {
                type: "area", width: 12
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', placeholder: 'Name here...', type: 'text', required: true, width: 6 },
                    { text: 'Description', field: 'description', placeholder: 'Description here...', type: 'textarea', required: true, width: 12 }
                ]
            },
        ]
    }

    return (<IUIPage schema={schema} />)
}

export const AddItemGroup = () => {
    const schema = {
        module: 'itemGroup',
        title: 'Item Group Management',
        back: true,
        fields: [
            {
                type: "area", width: 12
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', placeholder: 'Name here...', type: 'text', required: true, width: 6 },
                    { text: 'Description', field: 'description', placeholder: 'Description here...', type: 'textarea', required: true, width: 12 }
                ]
            },
        ]
    }

    return (<IUIPage schema={schema} />)
}