import IUIList from "../../common/IUIList";
import IUIPage from "../../common/IUIPage"

export const ListItemMaster = () => {

    const schema = {
        module: 'itemMaster',
        title: 'Item Master Management',
        paging: true,
        searching: true,
        editing: true,
        adding: true,
        fields: [
            { text: 'Name', field: 'name', type: 'link', sorting: true, searching: true },
            // {
            //     text: 'Group', field: 'itemGroupId', type: 'lookup', sorting: false, searching: false,
            //     schema: { module: 'itemGroup' }
            // },
            { text: 'Type', field: 'itemType', type: 'text', sorting: true, searching: true },
            { text: 'Description', field: 'description', type: 'text', sorting: false, searching: false }
        ]
    }


    return (<IUIList schema={schema} />)
}

export const ViewItemMaster = () => {
    const schema = {
        module: 'itemMaster',
        title: 'Item Master Management',
        editing: true,
        adding: false,
        back: true,
        readonly: true,
        fields: [
            {
                type: "area", width: 12
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', placeholder: 'Name here...', type: 'text', required: true, width: 12 },
                    {
                        text: 'Type', field: 'itemType', type: 'lookup', required: true, width: 6,
                        schema: {
                            items: [
                                { name: 'Fixed Asset' },
                                { name: 'Normal Consumption' },
                                { name: 'Service Assets' }
                            ]
                        }
                    },
                    {
                        text: 'Group', field: 'itemGroupId', type: 'lookup', required: false, width: 6,
                        schema: { module: 'itemGroup' }
                    },
                    { text: 'Description', field: 'description', placeholder: 'Description here...', type: 'textarea', required: true, width: 12 }
                ]
            },
        ]
    }

    return (<IUIPage schema={schema} />)
}

