import IUIList from "../../common/IUIList";
import IUIPage from "../../common/IUIPage"

export const ListProject = () => {

    const schema = {
        module: 'project',
        title: 'Project Management',
        paging: true,
        searching: true,
        editing: true,
        adding: true,
        fields: [
            { text: 'Name', field: 'name', type: 'link', sorting: true, searching: true },
            { text: 'Description', field: 'description', type: 'text', sorting: false, searching: false },
            { text: 'Start Date', field: 'startDate', type: 'date', sorting: false, searching: false },
            { text: 'End Date', field: 'endDate', type: 'date', sorting: false, searching: false }
        ]
    }


    return (<IUIList schema={schema} />)
}

export const ViewProject = () => {
    const schema = {
        module: 'project',
        title: 'Project Management',
        editing: true,
        adding: false,
        back: true,
        readonly: true,
        fields: [
            {
                type: "area", width: 12
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', width: 12, type: 'text', required: true },
                    { text: 'Start Date', field: 'startDate', width: 6, type: 'date', required: true },
                    { text: 'End Date', field: 'endDate', width: 6, type: 'date', required: true },
                    { text: 'Description', field: 'description', width: 12, type: 'textarea', required: true }
                ]
            },
            {
                type: "area", width: 12,
                fields: [
                    {
                        type: 'module-relation',
                        schema: {
                            module: 'tower',
                            title: 'Towers',
                            relationKey: "ProjectId",
                            paging: true,
                            searching: true,
                            editing: false,
                            adding: false,
                            fields: [
                                { text: 'Name', field: 'name', fieldIcon: 'object-group', placeholder: 'Name here...', type: 'text', required: true, width: 6 },
                                { text: 'Description', field: 'description', placeholder: 'Description here...', type: 'text', required: true, width: 6 },
                                // {
                                //     text: 'Project', field: 'projectId', type: 'lookup', required: false, width: 6,
                                //     schema: { module: 'project' }
                                // }
                            ]
                        }
                    }
                ]
            }
        ]
    }

    return (<IUIPage schema={schema} />)
}

export const EditProject = () => {
    const schema = {
        module: 'project',
        title: 'Project',
        back: false,
        fields: [
            {
                type: "area", width: 8
                , fields: [
                    { text: 'Name', field: 'name', fieldIcon: 'object-group', placeholder: 'Name here...', width: 6, type: 'text', required: true },
                    { text: 'Description', field: 'description', placeholder: 'Description here...', width: 6, type: 'textarea', required: true },
                    { text: 'Start Date', field: 'startDate', width: 6, type: 'date', required: true },
                    { text: 'End Date', field: 'endDate', width: 6, type: 'date', required: true },
                ]
            },
        ]
    }

    return (<IUIPage schema={schema} />)
}

